import React from "react";

import { Cell, CellTitle } from "../components/Cell";
import Layout from "../components/Layout";
import Para from "../components/Paragraph";
import Screenshots from "../components/Screenshots";
import { List, ListItem } from "../components/List";
import screenshot1 from "../img/wireframe/screenshot1.png";
import screenshot2 from "../img/wireframe/screenshot2.png";
import screenshot3 from "../img/wireframe/screenshot3.png";

const Wireframe = () => (
    <Layout subtitle={"Wireframe"}>
        <Cell>
            <CellTitle>Wireframe</CellTitle>

            <Para>
                A collection of shaders and materials for high-quality wireframe rendering in Unity.
                The package relies on geometry shaders so currently it runs only on Windows and
                DX10-compatible video cards. Geometry shaders are not yet supported by Unity on Linux and macOS.
            </Para>

            <Para>
                <a href="https://www.assetstore.unity3d.com/#/content/11638">Download from Unity Asset Store</a>.
            </Para>

            <Para>Features:</Para>

            <List>
                <ListItem>
                    Antialiased lines with adjustable width.
                </ListItem>

                <ListItem>
                    Lines are calculated in window space and don’t depend on object’s distance from the camera.
                    The farther the object is from the camera, the denser its wireframe.
                    This behavior is the same as for classic wireframe (used, for example, in Unity editor).
                </ListItem>

                <ListItem>
                    Deferred and forward rendering support.
                </ListItem>

                <ListItem>
                    Six example materials, including single side, two-sided, solid (wireframe + fill color).
                    Wireframe material can be mixed with any other material by simply assigning two (or more)
                    materials to the same object. This requires at least two draw calls though.
                    For simple wireframe rendering with fill color there is a shader included that
                    requires only one draw call.
                </ListItem>
            </List>

            <Para>
                <Screenshots urls={[screenshot1, screenshot2, screenshot3]}/>
            </Para>
        </Cell>
    </Layout>
);

export default Wireframe;
