import * as React from "react";
import styled from "styled-components";

import avatar from "../img/avatar.jpg";

const Root = styled.a`
    padding: 20px 15px;
    margin-bottom: 15px;
    background-color: #007fa5;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
`;

const Logo = styled.div`
    width: 80px;
    min-width: 80px;
    height: 80px;
    border-radius: 50%;
    background-image: url(${avatar});
    background-size: contain;
`;

const LogoTitle = styled.h1`
    margin-left: 20px;
    margin-bottom: 10px;
    font-size: 2rem;
    font-weight: bold;
    color: white;
    line-height: 2rem;
    letter-spacing: 2px;
    text-transform: uppercase;
`;

const LogoSubtitle = styled.div`
    margin-left: 20px;
    color: white;
    line-height: 1rem;
`;

export default class Header extends React.PureComponent {
    render() {
        return (
            <Root href="/">
                <Logo/>

                <div>
                    <LogoTitle>
                        Aleksey Fedotov
                    </LogoTitle>

                    <LogoSubtitle>
                        // home page of one programmer
                    </LogoSubtitle>
                </div>
            </Root>
        )
    }
}
